*{
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

.navbar{
  background-color: rgba(0, 0, 0, 0.9);
  color: #fff;
  display: flex;
  justify-content: space-around;
  position: sticky;
  width: 100%;
  align-items: center;
  padding: 0 10rem ;
}

.navbar a{
  color: rgb(204, 203, 203);
  text-decoration: none;
  font-size: .8rem;
}

.navbar .apple-logo{
  background: url(images/apple-logo.svg);
  background-repeat: no-repeat;
  height: 44px;
  width: 14px;
}

.navbar .search-logo{
  background: url(images/search-logo.svg);
  background-repeat: no-repeat;
  height: 44px;
  width: 14px;
}

.navbar .bag-logo{
  background: url(images/bag-logo.svg);
  background-repeat: no-repeat;
  height: 44px;
  width: 14px;
}

.main{
  width: 100%;
  height: 750px;
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: cover;
  background-image: url(images/iphone14.jpg);
}

.main .text-container{
  color: #fff;
  text-align: center;
  padding: 2rem 0;
  width: 100%;
}

.text-container h2{
  font-size: 50px;
  font-weight: 600;
  padding-bottom: .5rem;
}

.text-container h3{
  font-size: 30px;
  padding-bottom: .5rem;
  font-weight: 100;
}

.text-container p{
  font-size: 20px;
  color: rgb(118, 117, 117);
  padding-bottom: .5rem
}

.text-container a{
  padding: 0 3rem;
  font-size: 18px;
  color: #2997ff;
  text-decoration: none;
}

.content{
  background: url(images/iphone14-2.jpg);
  height: 600px;
  background-repeat: no-repeat;
  width: 100%;
  background-position: center;
}

.content .text-container{
  text-align: center;
  padding: 5% 0;
}

.content h3{
  color: #000;
}

.earpods{
  background: url(images/earpods.jpg);
  height: 800px;
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: bottom;
  margin-top: 1%;
}

.earpods .text-container{
  text-align: center;
  padding: 2rem 0;
}

.earpods .text-container h2,
h3{
  color: #fff;
}

.promos{
  margin-top: 1%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 1%;
  text-align: center;
  padding: 0 1rem;
}

.promos .text-container{
  padding: 0 2rem;
}

.watch-ultra{
  background: url(images/watch1.jpg);
  width: 100%;
  height: 400px;
  background-position: bottom ;
  background-repeat: no-repeat;
  background-size: cover;
}

.watch-ultra .logo-img{
  background: url(images/logo1.png);
  width: 100%;
  height: 42px;
  background-repeat: no-repeat;
  background-position: center ;
  margin-bottom: 2%;
  margin-top: 2%;
}

.watch-ultra h5{
  font-size: 25px;
  font-weight: 100;
  padding-bottom: .5rem;
}

.series-8{
  background: url(images/watch2.jpg);
  width: 100%;
  height: 450px;
  background-position: bottom ;
  background-repeat: no-repeat;
  background-size: cover; 
}

.series-8 .logo-img{
  background: url(images/logo2.png);
  width: 100%;
  height: 42px;
  background-repeat: no-repeat;
  background-position: center ;
  margin-bottom: 2%;
  margin-top: 2%;
}

.series-8 h5{
  color: #fff;
  font-size: 25px;
  font-weight: 100;
  padding-bottom: .5rem;
}

.fitness{
  background: url(images/weecknd.jpg);
  width: 100%;
  height: 450px;
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: cover; 
}

.fitness .logo-img{
  background: url(images/logo3.png);
  width: 100%;
  height: 42px;
  background-repeat: no-repeat;
  background-position: center ;
  margin-bottom: 1%;
  margin-top: 2%;
}

.fitness h5{
  color: #fff;
  font-size: 25px;
  font-weight: 100;
  padding-bottom: .5rem;
}

.fitness a{
  padding: 0 3rem;
  color: #a3ed24;
  font-size: 20px;
  text-decoration: none;
}

.watch-se{
  background: url(images/watch3.jpg);
  width: 100%;
  height: 450px;
  background-position: bottom ;
  background-repeat: no-repeat;
  background-size: cover; 
}

.watch-se .logo-img{
  background: url(images/logo4.png);
  width: 100%;
  height: 50px;
  background-repeat: no-repeat;
  background-position: center ;
  margin-bottom: 2%;
  margin-top: 2%;
}

.watch-se h5{
  color: #000;
  font-size: 25px;
  font-weight: 100;
  padding-bottom: .5rem;
}

.back-to-school{
  background: url(images/laptop1.jpg);
  width: 100%;
  height: 450px;
  background-position: bottom ;
  background-repeat: no-repeat;
  background-size: cover; 
}

.back-to-school p{
  color: rgb(179, 23, 25);
  padding: .2rem 0;
}

.back-to-school h4{
  font-size: 30px;
  padding-bottom: .5rem;
}

.back-to-school h5{
  font-size: 20px;
  font-weight: 100;
  padding-bottom: .5rem;
  line-height: 1.1;
}

.back-to-school a{
  font-size: 18px;
  color: #2997ff;
  text-decoration: none;
}

.card{
  background: url(images/sin1.jpg);
  width: 100%;
  height: 400px;
  background-position: bottom ;
  background-repeat: no-repeat;
  background-size: cover; 
}

.card .logo-img{
  background: url(images/logo1.png);
  width: 100%;
  height: 42px;
  background-repeat: no-repeat;
  background-position: center ;
  margin-bottom: 2%;
  margin-top: 2%
}

.card h5{
  font-size: 20px;
  font-weight: 100;
  padding-bottom: .5rem;
  line-height: 1.1;
}

.footer{
  padding: 0 2rem;
  margin-top: 3%;
  background-color: #f5f5f7;
}

.footer li{
  list-style: none;
  padding-bottom: .5rem;
  font-size: 14px;
  color: #888889;;
}

.footer a{
  text-decoration: none;
  color: #888889;
}

.notes{
  border-bottom: 1px solid #d2d2d7;
  padding-bottom: 1rem;
}

.directory{
  display: flex;
  padding: 2rem 0;
  justify-content: space-around;
}

.directory h3{
  color: #000;
  font-weight: 100;
  font-size: 15px;
  padding-bottom: .5rem;
}

.col-2 div{
  padding-bottom: 1rem;
}

.col-4 div{
  padding-bottom: 1rem;
}

.col-5 div{
  padding-bottom: 1rem;
}

.shop{
  padding-bottom: .5rem;
  border-bottom: 1px solid #d2d2d7;
  font-size: 14px;
}

.shop a{
  color: #2997ff;
}

.footer-links{
  display: flex;
  justify-content: space-between;
  padding: 1rem 0;
}

.footer-links ul{
  display: flex;
}
.footer-links ul li{
  padding-right: 1rem;
}

.navbar a:hover{
  color: #fff;
  transition: 2s;
}

.text-container a:hover{
  text-decoration: underline;
  transition: 2s;
}

.footer a:hover{
  text-decoration: underline;
  transition: 2s;
}